
import { defineComponent } from 'vue'

export default defineComponent({
    emits: ['onindicatorsSelected'],
    props: {
        siteName: {
            type: String,
            default: 'N/A'
        },
        date: {
            type: String,
            default: 'N/A'
        },
        reportingMonth: {
            type: String,
            default: 'N/A'
        },
        reportingYear: {
            type: String,
            default: 'N/A'
        },
        indicators: {
            type: Object,
            required: true
        }
    },
    setup(props, {emit}) {
        return {
            drill: (indicator: string) => {
                emit('onindicatorsSelected', indicator)
            }
        }
    }
})
